// ==================================================================================
// üé® 2. ÁªàÊûÅÊ∏≤ÊüìÂô®
// ==================================================================================
const InlineText = ({ text }: { text: string }) => {
  if (!text) return null;
  const parts = text.split(/(\*\*.*?\*\*)/g);
  return (
    <>
      {parts.map((part, index) => {
        if (part.startsWith("**") && part.endsWith("**")) {
          return <strong key={index} className="font-bold text-[#0F766E]">{part.slice(2, -2)}</strong>;
        }
        return <span key={index}>{part}</span>;
      })}
    </>
  );
};

function UltimateRenderer({ content }: { content: string }) {
  if (!content) return <span className="animate-pulse text-primary">‚ñå</span>;

  const lines = content.split("\n");
  const renderedElements = [];
  let tableBuffer: string[] = [];

  for (let i = 0; i < lines.length; i++) {
    const line = lines[i];
    const isTableLine = line.trim().startsWith("|") && line.includes("|");

    if (isTableLine) {
      tableBuffer.push(line);
      if (i === lines.length - 1 || !lines[i + 1].trim().startsWith("|")) {
        renderedElements.push(<TableBlock key={`tbl-${i}`} lines={tableBuffer} />);
        tableBuffer = [];
      }
      continue;
    }

    if (line.trim().startsWith("# ")) {
      renderedElements.push(<h1 key={i} className="mt-8 mb-4 text-2xl font-extrabold text-[#0F766E] border-b-2 border-primary/10 pb-3">{line.replace(/^#\s+/, "")}</h1>);
    } else if (line.trim().startsWith("## ")) {
      renderedElements.push(<h3 key={i} className="mt-6 mb-3 text-lg font-bold text-gray-900 border-b border-gray-100 pb-2">{line.replace(/^##\s+/, "")}</h3>);
    } else if (line.trim().startsWith("### ")) {
      renderedElements.push(<h4 key={i} className="mt-4 mb-2 text-base font-bold text-gray-800">{line.replace(/^###\s+/, "")}</h4>);
    } else if (line.trim().startsWith("- ")) {
      renderedElements.push(
        <div key={i} className="flex gap-2 ml-2 my-1 text-sm text-gray-700 leading-7">
          <span className="text-primary/70 font-bold">‚Ä¢</span>
          <span><InlineText text={line.replace(/^- /, "")} /></span>
        </div>
      );
    } else if (line.trim().startsWith("> ")) {
      renderedElements.push(
        <blockquote key={i} className="my-3 border-l-4 border-primary/40 bg-teal-50/30 pl-4 py-3 italic text-gray-600 text-sm rounded-r-lg">
          <InlineText text={line.replace(/^> /, "")} />
        </blockquote>
      );
    } else if (line.trim() === "---") {
      renderedElements.push(<hr key={i} className="my-6 border-gray-200 dashed" />);
    } else if (line.trim() === "") {
      renderedElements.push(<div key={i} className="h-2"></div>);
    } else {
      renderedElements.push(
        <p key={i} className="text-[15px] leading-7 text-gray-700 my-1">
          <InlineText text={line} />
        </p>
      );
    }
  }
  return <div className="w-full">{renderedElements}</div>;
}

const TableBlock = ({ lines }: { lines: string[] }) => {
  if (lines.length < 2) return null;
  try {
    const headerLine = lines.find(l => l.includes("|") && !l.includes("---"));
    const bodyLines = lines.filter(l => l.includes("|") && !l.includes("---") && l !== headerLine);
    if (!headerLine) return null;
    const headers = headerLine.split("|").filter(c => c.trim()).map(c => c.trim());
    return (
      <div className="my-5 overflow-hidden rounded-xl border border-gray-200 shadow-sm w-full">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-[#1a2e35] text-white">
              <tr>
                {headers.map((h, i) => (
                  <th key={i} className="px-5 py-3 text-left text-xs font-semibold tracking-wider whitespace-nowrap uppercase">{h}</th>
                ))}
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-100">
              {bodyLines.map((line, i) => {
                const cells = line.split("|").filter(c => c.trim()).map(c => c.trim());
                return (
                  <tr key={i} className="hover:bg-gray-50 transition-colors">
                    {cells.map((cell, j) => (
                      <td key={j} className="px-5 py-3 text-sm text-gray-700 leading-relaxed min-w-[120px]"><InlineText text={cell} /></td>
                    ))}
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    );
  } catch (e) { return null; }
};

// ==================================================================================

type UploadedFile = {
  name: string
  type: string
  size: number
  data: string
  preview?: string
  difyFileId?: string
}

type Message = {
  id: string
  role: "user" | "assistant"
  content: string
}

type FileProcessingState = {
  status: "idle" | "uploading" | "processing" | "recognizing" | "complete" | "error"
  progress: number
  message: string
}

export function EnhancedChatInterface() {
  const [userId, setUserId] = useState<string>("")
  const sessionIdRef = useRef<string | null>(null)

  useEffect(() => {
    if (typeof window !== 'undefined') {
      const userStr = localStorage.getItem('currentUser')
      if (userStr) {
        try {
          const user = JSON.parse(userStr)
          setUserId(user.id || user.sub || user.userId || "")
        } catch (e) {}
      }
    }
  }, [])

  const [messages, setMessages] = useState<Message[]>([])
  const [input, setInput] = useState("")
  const [isLoading, setIsLoading] = useState(false)
  const [uploadedFiles, setUploadedFiles] = useState<UploadedFile[]>([])
  const [copiedMessageId, setCopiedMessageId] = useState<string | null>(null)
  
  const [analysisStage, setAnalysisStage] = useState(0)
  const [fileProcessing, setFileProcessing] = useState<FileProcessingState>({
    status: "idle", progress: 0, message: ""
  })

  // Ê†áËÆ∞ÊòØÂê¶‰∏∫‚ÄúÂ§çÊùÇÊ®°Âºè‚Äù (ÊúâÂõæ OR ÈïøÊñá)
  const [isComplexMode, setIsComplexMode] = useState(false)

  const fileInputRef = useRef<HTMLInputElement>(null)
  const messagesEndRef = useRef<HTMLDivElement>(null)
  const textareaRef = useRef<HTMLTextAreaElement>(null)

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }, [messages])

  useEffect(() => {
    if (isLoading && isComplexMode && analysisStage < 4) {
      const timer = setTimeout(() => setAnalysisStage((prev) => Math.min(prev + 1, 4)), 2000)
      return () => clearTimeout(timer)
    }
  }, [isLoading, analysisStage, isComplexMode])

  const handleFileUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const files = event.target.files
    if (!files || files.length === 0) return

    setFileProcessing({ status: "uploading", progress: 0, message: "Ê≠£Âú®‰∏ä‰º†Êñá‰ª∂..." })
    const newFiles: UploadedFile[] = []
    const totalFiles = files.length

    for (let i = 0; i < files.length; i++) {
      const file = files[i]
      setFileProcessing({ status: "uploading", progress: Math.round(((i + 0.5) / totalFiles) * 100), message: `Ê≠£Âú®‰∏ä‰º† ${file.name}...` })

      try {
        const formData = new FormData()
        formData.append("file", file)
        formData.append("user", userId) 

        const uploadResponse = await fetch("/api/dify-upload", { method: "POST", body: formData })
        if (!uploadResponse.ok) throw new Error(`‰∏ä‰º†Â§±Ë¥•: ${file.name}`)

        const uploadData = await uploadResponse.json()
        const reader = new FileReader()
        const fileData = await new Promise<string>((resolve) => {
          reader.onload = (e) => resolve(e.target?.result as string)
          reader.readAsDataURL(file)
        })

        const uploadedFile: UploadedFile = {
          name: file.name, type: file.type, size: file.size, data: fileData, difyFileId: uploadData.id
        }
        if (file.type.startsWith("image/")) uploadedFile.preview = fileData
        newFiles.push(uploadedFile)
      } catch (error) {
        toast.error(`${file.name} ‰∏ä‰º†Â§±Ë¥•`)
      }
    }

    if (newFiles.length > 0) {
      setFileProcessing({ status: "complete", progress: 100, message: `Â∑≤‰∏ä‰º† ${newFiles.length} ‰∏™Êñá‰ª∂` })
      setUploadedFiles((prev) => [...prev, ...newFiles])
    } else {
      setFileProcessing({ status: "error", progress: 0, message: "Êñá‰ª∂‰∏ä‰º†Â§±Ë¥•" })
    }
    setTimeout(() => setFileProcessing({ status: "idle", progress: 0, message: "" }), 2000)
    if (fileInputRef.current) fileInputRef.current.value = ""
  }

  const removeFile = (index: number) => setUploadedFiles((prev) => prev.filter((_, i) => i !== index))

  const onSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!userId) { toast.error("ËØ∑ÂÖàÁôªÂΩï"); return }
    const trimmedInput = (input || "").trim()
    if (!trimmedInput && uploadedFiles.length === 0) return

    setIsLoading(true)
    setAnalysisStage(0)

    // ‚úÖ Ê†∏ÂøÉ‰øÆÂ§çÔºöÊô∫ËÉΩÂà§Êñ≠ÊòØÂê¶ÂºÄÂêØ‚ÄúÂ§ßÂä®Áîª‚Äù
    // Êù°‰ª∂ÔºöÊúâÊñá‰ª∂ OR ÊñáÂ≠óÈïøÂ∫¶Ë∂ÖËøá 150 Â≠ó
    if (uploadedFiles.length > 0 || trimmedInput.length > 150) {
      setIsComplexMode(true)
    } else {
      setIsComplexMode(false)
    }

    const messageContent = trimmedInput || "ËØ∑Â∏ÆÊàëÊâπÊîπËøôÁØá‰ΩúÊñá"
    const fileIds = uploadedFiles.filter((f) => f.difyFileId).map((f) => f.difyFileId)

    const userMessage: Message = { id: Date.now().toString(), role: "user", content: messageContent }
    setMessages((prev) => [...prev, userMessage])
    setInput("")
    setUploadedFiles([])

    const assistantMessageId = (Date.now() + 1).toString()
    setMessages((prev) => [...prev, { id: assistantMessageId, role: "assistant", content: "" }])

    let accumulatedContent = ""
    let hasReceivedContent = false

    try {
      console.log("üöÄ [Frontend] Sending to Backend (Session:", sessionIdRef.current, ")");

      const requestBody = {
        query: messageContent,
        fileIds: fileIds,
        inputs: {},
        userId: userId,
        conversation_id: sessionIdRef.current, 
      }

      const response = await fetch("/api/dify-chat", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(requestBody),
      })

      if (response.status === 402) {
        const errorData = await response.json()
        throw new Error(errorData.error || "ÁßØÂàÜ‰∏çË∂≥")
      }
      if (!response.ok) throw new Error(`ËØ∑Ê±ÇÂ§±Ë¥•: ${response.status}`)

      const reader = response.body?.getReader()
      if (!reader) throw new Error("Êó†Ê≥ïËØªÂèñÂìçÂ∫îÊµÅ")

      const decoder = new TextDecoder()
      let buffer = ""

      while (true) {
        const { done, value } = await reader.read()
        if (done) break

        const chunk = decoder.decode(value, { stream: true })
        buffer += chunk
        const lines = buffer.split("\n")
        buffer = lines.pop() || ""

        for (const line of lines) {
          if (!line.trim() || !line.startsWith("data: ")) continue
          const data = line.slice(6).trim()
          if (data === "[DONE]") continue

          try {
            const parsed = JSON.parse(data)
            
            if (parsed.conversation_id && sessionIdRef.current !== parsed.conversation_id) {
                 sessionIdRef.current = parsed.conversation_id
            }

            if (parsed.event === "message" || parsed.event === "agent_message") {
              if (parsed.answer) {
                if (!hasReceivedContent) setAnalysisStage(4)
                accumulatedContent += parsed.answer
                hasReceivedContent = true
                setMessages((prev) =>
                  prev.map((msg) => (msg.id === assistantMessageId ? { ...msg, content: accumulatedContent } : msg)),
                )
              }
            } else if (parsed.event === "error") {
              throw new Error(parsed.message || "DifyÂ§ÑÁêÜÈîôËØØ")
            }
          } catch (parseError) {}
        }
      }
      if (hasReceivedContent) toast.success("ÂõûÂ§çÂÆåÊàê")
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : "Êú™Áü•ÈîôËØØ"
      if (errorMessage.includes("ÁßØÂàÜ‰∏çË∂≥")) {
        toast.error(errorMessage)
        setMessages((prev) => prev.filter(msg => msg.id !== assistantMessageId))
      } else {
        setMessages((prev) =>
          prev.map((msg) => (msg.id === assistantMessageId ? { ...msg, content: `ÈîôËØØÔºö${errorMessage}` } : msg))
        )
        toast.error(errorMessage)
      }
    } finally {
      setIsLoading(false)
      setAnalysisStage(0)
    }
  }

  const handleKeyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (e.key === "Enter" && !e.shiftKey) { e.preventDefault(); onSubmit(e as unknown as React.FormEvent) }
  }

  return (
    <div className="flex h-screen flex-col bg-gradient-to-b from-amber-50/50 to-white">
      <ScrollArea className="flex-1">
        <div className="mx-auto max-w-4xl px-4 py-8">
          {messages.length === 0 ? (
            <div className="flex flex-col items-center justify-center py-20 text-center">
              <div className="mb-6 flex h-16 w-16 items-center justify-center rounded-2xl bg-primary/10 shadow-lg">
                <FileText className="h-8 w-8 text-primary" />
              </div>
              <h1 className="mb-4 text-2xl font-bold text-foreground">‰Ω†Â•ΩÔºÅÊ¨¢Ëøé‰ΩøÁî®Ê≤àÁøîÊô∫Â≠¶ÔºÅ</h1>
              <p className="mb-2 max-w-md text-muted-foreground">‰∏ì‰∏öÁöÑ‰ΩúÊñáÊâπÊîπ‰∏ìÂÆ∂Ôºå‰∏∫Â≠¶Áîü‰π†‰ΩúÊèê‰æõÊ∑±Â∫¶ÁÇπËØÑ„ÄÇ</p>
            </div>
          ) : (
            <div className="space-y-6">
              {messages.map((message, index) => (
                <div key={message.id} className={cn("flex gap-4", message.role === "user" ? "justify-end" : "justify-start")}>
                  {message.role === "assistant" && (
                    <div className="flex h-10 w-10 shrink-0 items-center justify-center rounded-xl bg-primary shadow-sm text-white">
                      <Sparkles className="h-5 w-5" />
                    </div>
                  )}
                  <div className={cn("rounded-2xl px-4 py-3 shadow-sm", message.role === "user" ? "bg-primary text-primary-foreground max-w-[85%]" : "bg-white border border-border/50 w-full max-w-full")}>
                    {message.role === "user" ? (
                      <div className="whitespace-pre-wrap text-sm leading-relaxed">{message.content}</div>
                    ) : (
                       isLoading && !message.content ? (
                          isComplexMode ? <AnalysisStages /> : <SimpleBrainLoader />
                       ) : <UltimateRenderer content={message.content} />
                    )}
                    {message.role === "assistant" && message.content && (
                      <div className="mt-3 flex items-center gap-2 border-t border-border/30 pt-3">
                        <Button variant="ghost" size="sm" className="h-8 gap-1.5 text-xs text-muted-foreground hover:text-foreground" onClick={() => navigator.clipboard.writeText(message.content).then(() => toast.success("Â∑≤Â§çÂà∂"))}>
                           <Copy className="h-3.5 w-3.5" /> Â§çÂà∂
                        </Button>
                      </div>
                    )}
                  </div>
                  {message.role === "user" && <div className="flex h-10 w-10 shrink-0 items-center justify-center rounded-xl bg-primary shadow-sm"><User className="h-5 w-5 text-primary-foreground" /></div>}
                </div>
              ))}
              <div ref={messagesEndRef} />
            </div>
          )}
        </div>
      </ScrollArea>

      <div className="border-t border-border/50 bg-white/80 backdrop-blur-sm">
        <div className="mx-auto max-w-4xl px-4 py-4">
          {fileProcessing.status !== "idle" && (
            <div className="mb-4 rounded-xl border border-border/50 bg-white p-4 shadow-sm">
              <div className="flex items-center gap-3">
                {fileProcessing.status === "error" ? <AlertCircle className="h-5 w-5 text-destructive" /> : <Loader2 className="h-5 w-5 animate-spin text-primary" />}
                <p className="text-sm font-medium text-foreground">{fileProcessing.message}</p>
              </div>
            </div>
          )}
          {uploadedFiles.length > 0 && (
            <div className="mb-4 flex flex-wrap gap-2">
              {uploadedFiles.map((file, index) => (
                <div key={index} className="flex items-center gap-2 rounded-xl border border-border/50 bg-white px-3 py-2 shadow-sm">
                  {file.preview ? <img src={file.preview} alt="" className="h-12 w-12 rounded-lg object-cover" /> : <FileText className="h-5 w-5 text-muted-foreground" />}
                  <span className="max-w-[120px] truncate text-sm text-foreground">{file.name}</span>
                  <button onClick={() => removeFile(index)}><X className="h-4 w-4" /></button>
                </div>
              ))}
            </div>
          )}

          <form onSubmit={onSubmit} className="relative">
            <div className="flex items-end gap-3 rounded-2xl border border-border/50 bg-white p-3 shadow-sm focus-within:border-primary/50 focus-within:ring-2 focus-within:ring-primary/20">
              <Button type="button" variant="ghost" size="icon" className="h-10 w-10 shrink-0 rounded-xl" onClick={() => fileInputRef.current?.click()} disabled={isLoading}>
                <Paperclip className="h-5 w-5" />
              </Button>
              <input ref={fileInputRef} type="file" className="hidden" accept="image/*,.txt,.doc,.docx,.pdf" multiple onChange={handleFileUpload} />
              <Textarea
                ref={textareaRef}
                value={input}
                onChange={(e) => setInput(e.target.value)}
                onKeyDown={handleKeyDown}
                placeholder={userId ? "ËæìÂÖ•‰ΩúÊñáÂÜÖÂÆπÊàñ‰∏ä‰º†ÂõæÁâá..." : "ËØ∑ÂÖàÁôªÂΩï..."}
                className="min-h-[44px] max-h-[200px] flex-1 resize-none border-0 bg-transparent p-0 text-foreground placeholder:text-muted-foreground/60 focus-visible:ring-0"
                disabled={isLoading}
                rows={1}
              />
              <Button type="submit" size="icon" className="h-10 w-10 shrink-0 rounded-xl bg-primary text-primary-foreground shadow-sm" disabled={isLoading || (!input.trim() && uploadedFiles.length === 0)}>
                {isLoading ? <Loader2 className="h-5 w-5 animate-spin" /> : <Send className="h-5 w-5" />}
              </Button>
            </div>
          </form>
          <p className="mt-3 text-center text-xs text-muted-foreground/60">
  {userId ? "Ê∑±Â∫¶ÊâπÊîπ 250 ÁßØÂàÜ / ÊôÆÈÄöÂØπËØù 20 ÁßØÂàÜ" : "Êú™ÁôªÂΩï - ËØ∑ÂÖàÁôªÂΩï‰ª•‰ΩøÁî® AI ÂäüËÉΩ"}
</p>
        </div>
      </div>
    </div>
  )
}